.layout {
  min-height: 100vh;
  min-width: 1500px;
}
.layout .logo img {
  height: 40px;
}
.layout .trigger {
  font-size: 18px;
  line-height: 64px;
  padding: 0 24px;
  cursor: pointer;
  transition: color 0.3s;
}
.layout .trigger:hover {
  color: #1890ff;
}
.layout .content {
  margin: 0 16px;
}
.layout .content .breadcrumb {
  margin: 16px 0;
}
.layout .layout-main {
  min-height: calc(100vh - 214px);
  padding: 24px;
  background: #fff;
  border: 1px solid #e8e8e8;
  border-top: none;
}
.layout .layout-tabs {
  margin-top: 20px;
}
.layout .layout-tabs .ant-tabs-bar {
  margin: 0;
}
.layout .ant-table-small > .ant-table-content > .ant-table-body {
  margin: 0;
}
.layout .ant-table-small > .ant-table-content > .ant-table-body > table > .ant-table-thead > tr > th {
  background-color: #fafafa;
}
.ant-layout-sider,
.ant-menu-dark {
  background-color: #224b8f;
}
.ant-menu-dark li {
  border-bottom: 1px solid #295caa;
}
.ant-layout-header {
  background-color: #295caa !important;
  color: white;
}
.ant-menu-dark .ant-menu-inline.ant-menu-sub {
  background-color: #295caa;
  box-shadow: 0 2px 8px rgba(41, 92, 170, 0.46) inset;
}
